* {
  margin: 0;
  padding: 0;
}


html {
  /* font-size: 16px; */
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(241, 241, 247);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.flex-cc {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-sc {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-lc {
  display: flex;
  justify-content: left;
  align-items: center;
}

.flex-ls {
  display:flex;
  justify-content: left;
  align-items: start;
}

.flex-ss {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}

.rotate {
  transform: rotate(180deg);
}

img {
  transition:  transform 0.5s ease;
}












/* for CSSTransition component */

.fade-enter {
  opacity: 0; 
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in-out ;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-in-out, transform 500ms;
}